<template>
  <div class="main-content">

    <!--  -->
    <div v-if="showFlag">
      <el-form :inline="true" :model="searchForm" class="form-content">
        <el-row :gutter="20" class="slt"
                :style="{justifyContent:contents.searchBoxPosition=='1'?'flex-start':contents.searchBoxPosition=='2'?'center':'flex-end'}">

          <el-form-item :label="contents.inputTitle == 1 ? '' : ''">
            <el-input prefix-icon="el-icon-search" v-model="searchForm.nongziName" placeholder=""
                      clearable></el-input>
          </el-form-item>

          <el-form-item :label="contents.inputTitle == 1 ? '' : ''">
            <el-select v-model="searchForm.nongziTypes" placeholder="">
              <el-option label="=--=" value=""></el-option>
              <el-option
                  v-for="(item,index) in nongziTypesSelectSearch"
                  v-bind:key="index"
                  :label="item.indexName"
                  :value="item.codeIndex">
                <!--lable-->
                <!--value-->
              </el-option>
            </el-select>
          </el-form-item>


          <el-form-item>
            <el-button type="success" @click="search()"><i class="el-icon-search el-icon--right"/></el-button>
          </el-form-item>
        </el-row>
        <el-row class="ad"
                :style="{justifyContent:contents.btnAdAllBoxPosition=='1'?'flex-start':contents.btnAdAllBoxPosition=='2'?'center':'flex-end'}">
          <el-form-item>
            <el-button
                v-if="isAuth('nongzi','')"
                type="success"
                icon="el-icon-plus"
                @click="addOrUpdateHandler()"
            >
            </el-button>
            &nbsp;
            <el-button
                v-if="isAuth('nongzi','')"
                :disabled="dataListSelections.length <= 0"
                type="danger"
                icon="el-icon-delete"
                @click="deleteHandler()"
            >
            </el-button>
            &nbsp;
            <el-button
                v-if="isAuth('nongzi','')"
                type="success"
                icon="el-icon-pie-chart"
                @click="chartDialog()"
            >
            </el-button>
            &nbsp;
            <a style="text-decoration:none" class="el-button el-button--success"
               v-if="isAuth('nongzi','')"
               icon="el-icon-download"
               href="http://localhost:8080/lhcnyxxglxt/upload/nongziMuBan.xls"
            ></a>
            &nbsp;
            <el-upload
                v-if="isAuth('nongzi','')"
                style="display: inline-block"
                :action="getActionUrl"
                :on-success="nongziUploadSuccess"
                :on-error="nongziUploadError"
                :show-file-list=false>
              <el-button
                  v-if="isAuth('nongzi','')"
                  type="success"
                  icon="el-icon-upload2"
              >
              </el-button>
            </el-upload>
            &nbsp;
            <!-- excel -->
            <download-excel v-if="isAuth('nongzi','')" style="display: inline-block"
                            class="export-excel-wrapper" :data="dataList" :fields="json_fields" name="nongzi.xls">
              <!-- excel -->
              <el-button
                  type="success"
                  icon="el-icon-download"
              >
              </el-button>
            </download-excel>
            &nbsp;
          </el-form-item>
        </el-row>
      </el-form>
      <div class="table-content">
        <el-table class="tables" :size="contents.tableSize" :show-header="contents.tableShowHeader"
                  :header-row-style="headerRowStyle" :header-cell-style="headerCellStyle"
                  :border="contents.tableBorder"
                  :fit="contents.tableFit"
                  :stripe="contents.tableStripe"
                  :row-style="rowStyle"
                  :cell-style="cellStyle"
                  :style="{width: '100%',fontSize:contents.tableContentFontSize,color:contents.tableContentFontColor}"
                  v-if="isAuth('nongzi','')"
                  :data="dataList"
                  v-loading="dataListLoading"
                  @selection-change="selectionChangeHandler">
          <el-table-column v-if="contents.tableSelection"
                           type="selection"
                           header-align="center"
                           align="center"
                           width="50">
          </el-table-column>
          <el-table-column label="" v-if="contents.tableIndex" type="index" width="50"/>

          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="nongziName"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.nongziName }}
            </template>
          </el-table-column>

          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="nongziUuidNumber"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.nongziUuidNumber }}
            </template>
          </el-table-column>
          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="nongziPhoto"
                           header-align="center"
                           width="200"
                           label="">
            <template slot-scope="scope">
              <div v-if="scope.row.nongziPhoto">
                <img :src="$base.url+scope.row.nongziPhoto" width="100" height="100">
              </div>
              <div v-else></div>
            </template>
          </el-table-column>
          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="nongziTypes"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.nongziValue }}
            </template>
          </el-table-column>
          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="nongziKucunNumber"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.nongziKucunNumber }}
            </template>
          </el-table-column>

          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="nongziNewMoney"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.nongziNewMoney }}
            </template>
          </el-table-column>
          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="nongziClicknum"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.nongziClicknum }}
            </template>
          </el-table-column>

          <el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
                           prop="insertTime"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              {{ scope.row.insertTime }}
            </template>
          </el-table-column>

          <el-table-column width="300" :align="contents.tableAlign"
                           header-align="center"
                           label="">
            <template slot-scope="scope">
              <el-button v-if="sessionTable=='false'" type="primary" size="mini" @click="wuyong(scope.row.id)">
                
              </el-button>
              <el-button v-if="sessionTable=='false'" type="success" icon="el-icon-printer" size="mini"
                         @click="dayinOpen(scope.row)">
              </el-button>
              <el-button v-if="isAuth('nongzi','')" type="success" icon="el-icon-tickets" size="mini"
                         @click="addOrUpdateHandler(scope.row.id,'info')">
              </el-button>
              <el-button v-if="isAuth('nongzi','')" type="primary" icon="el-icon-edit" size="mini"
                         @click="addOrUpdateHandler(scope.row.id)">
              </el-button>
              <el-button v-if="isAuth('nongzi','')" type="danger" icon="el-icon-delete" size="mini"
                         @click="deleteHandler(scope.row.id)">
              </el-button>
<!--              <el-button v-if="isAuth('nongzi','')" type="success" icon="el-icon-tickets" size="mini"-->
<!--                         @click="goumai(scope.row)">-->
<!--              </el-button>-->
              <!--              <el-button v-if="isAuth('nongzi','')" type="primary" icon="el-icon-add-location" size="mini" @click="plusNongziKucunNumber(scope.row.id,scope.row.nongziKucunNumber)"></el-button>-->
              <!--              <el-button v-if="isAuth('nongzi','')" type="primary" icon="el-icon-delete-location" size="mini" @click="reduceNongziKucunNumber(scope.row.id,scope.row.nongziKucunNumber)"></el-button>-->

            </template>
          </el-table-column>
        </el-table>
        <el-pagination
            clsss="pages"
            :layout="layouts"
            @size-change="sizeChangeHandle"
            @current-change="currentChangeHandle"
            :current-page="pageIndex"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="Number(contents.pageEachNum)"
            :total="totalPage"
            :small="contents.pageStyle"
            class="pagination-content"
            :background="contents.pageBtnBG"
            :style="{textAlign:contents.pagePosition==1?'left':contents.pagePosition==2?'center':'right'}"
        ></el-pagination>
      </div>
    </div>
    <!-- /  search-->
    <add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

    <!--  -->
    <el-dialog
        :title="nongziKucunNumberTitle"
        :visible.sync="nongziKucunNumberVisible"
        width="30%">
      <span></span>
      <el-input-number v-model="kucunNumber" :min="1" :max="100" label=""></el-input-number>
      <span slot="footer" class="dialog-footer">
			<el-button @click="nongziKucunNumberVisible = false"> </el-button>
			<el-button type="primary" @click="addNongziKucunNumber()"> </el-button>
		  </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="printVisiable" width="500px">
      <el-form ref="printContent" :model="printFrom" label-width="auto">
        <el-form-item label="">
          <div style="display: flex">
            <el-input v-model="printFrom.kuaidiFajianName" disabled></el-input>
            :
            <el-input v-model="printFrom.kuaidiFajianShoujihao" disabled></el-input>
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="printFrom.kuaidiFajianAddress" disabled></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
              <el-button @click="printContent()" icon="el-icon-printer" type="success"></el-button>
              <el-button @click="printVisiable = false"></el-button>
            </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="printVisiable1" width="500px">
      <el-form ref="printContent" :model="printFrom" label-width="auto">
<!--        <el-form-item label="">-->
<!--          <div style="display: flex">-->
<!--            <el-input v-model="printFrom.kuaidiFajianName" disabled></el-input>-->
<!--            :-->
<!--            <el-input v-model="printFrom.kuaidiFajianShoujihao" disabled></el-input>-->
<!--          </div>-->
<!--        </el-form-item>-->
        <el-form-item label="">
          <el-input v-model="goumaishuliang" ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="shouhudizhi" ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
              <el-button @click="tijiao()" icon="el-icon-printer" type="success"></el-button>
              <el-button @click="printVisiable1 = false"></el-button>
            </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="chartVisiable" width="80%">
      <el-date-picker v-model="echartsDate" type="year" placeholder=""></el-date-picker>
      <el-button @click="chartDialog()"></el-button>
      <div id="statistic" style="width:100%;height:600px;"></div>

      <span slot="footer" class="dialog-footer">
				<el-button @click="chartVisiable = false"></el-button>
			</span>
    </el-dialog>

  </div>
</template>
<script>
import AddOrUpdate from "./add-or-update";
import styleJs from "../../../utils/style.js";
import utilsJs, {getYearFormat, getMonthFormat, getDateFormat, getDatetimeFormat} from "../../../utils/utils.js";

export default {
  data() {
    return {
      //
      printFrom: {
        name1: "",
      },

      printVisiable: false,
      //
      searchForm: {
        key: ""
      },
      sessionTable: "",//
      role: "",//
      userId: "",//id
      //
      //
      nongziTypesSelectSearch: [],
      form: {
        id: null,
        nongziName: null,
        nongziUuidNumber: null,
        nongziPhoto: null,
        nongziTypes: null,
        nongziKucunNumber: null,
        nongziNewMoney: null,
        nongziClicknum: null,
        nongziContent: null,
        nongziDelete: null,
        insertTime: null,
        createTime: null,
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      showFlag: true,
      sfshVisiable: false,
      shForm: {},
      chartVisiable: false,
      echartsDate: new Date(),//echarts
      addOrUpdateFlag: false,
      contents: null,
      layouts: '',
      printVisiable1:false,
      shouhudizhi:'',
      goumaishuliang:'',
      nongziData:[],
      //excel
      json_fields: {
        //
        //
        '': "nongziName",
        '': "nongziUuidNumber",
        '': "nongziPhoto",
        '': "nongziValue",
        '': "nongziKucunNumber",
        '': "nongziNewMoney",
        '': "nongziClicknum",
        '': "insertTime",
      },

      //
      //
      kucunNumber: 1,
      //id
      kucunNumberId: null,
      //
      kucunNumberTypes: null,
      //
      nongziKucunNumber: 0,
      //
      nongziKucunNumberTitle: null,
      //
      nongziKucunNumberVisible: false,
    };
  },
  created() {
    this.contents = styleJs.listStyle();
    this.init();
    this.getDataList();
    this.contentStyleChange()
  },
  mounted() {
    //
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    this.userId = this.$storage.get("userId");

  },
  filters: {
    htmlfilter: function (val) {
      return val.replace(/<[^>]*>/g).replace(/undefined/g, '');
    }
  },
  components: {
    AddOrUpdate,
  },
  computed: {
    getActionUrl: function () {
      return `/` + this.$base.name + `/file/upload`;
    }
  },
  methods: {
    chartDialog() {
      let _this = this;
      let params = {
        dateFormat: "%Y", //%Y-%m
        riqi: getYearFormat(_this.echartsDate),//
        // riqi :getMonthFormat(_this.echartsDate),//
        thisTable: {//
          tableName: 'nongzi',//,
          sumColum: 'nongzi_number', //
          date: 'insert_time',//
          // string : 'nongzi_name',//
          // types : 'nongzi_types',//
        },
        // joinTable : {//
        //     tableName :'yonghu',//
        //     // date : 'insert_time',//
        //     string : 'yonghu_name',//
        //     // types : 'yonghu_types',//
        // }
      }
      _this.chartVisiable = true;
      _this.$nextTick(() => {
        var statistic = this.$echarts.init(document.getElementById("statistic"), 'macarons');
        this.$http({
          url: "barSum",//barCountOne barCountTwo barSumOne barSumTwo
          method: "get",
          params: params
        }).then(({data}) => {
          if (data && data.code === 0) {
            let yAxisName = "";//y
            let xAxisName = "";//x
            let series = [];//
            data.data.yAxis.forEach(function (item, index) {//
              let tempMap = {};
              tempMap.name = data.data.legend[index];
              tempMap.type = 'bar';
              tempMap.data = item;
              series.push(tempMap);
            })

            var option = {
              tooltip: {
                trigger: 'axis',
                axisPointer: {
                  type: 'cross',
                  crossStyle: {
                    color: '#999'
                  }
                }
              },
              toolbox: {
                feature: {
                  // dataView: { show: true, readOnly: false },  // 
                  magicType: {show: true, type: ['line', 'bar']},//
                  // restore: { show: true }, // 
                  saveAsImage: {show: true}//
                }
              },
              legend: {
                data: data.data.legend//  
              },
              xAxis: [
                {
                  type: 'category',
                  axisLabel: {interval: 0},
                  name: xAxisName,
                  data: data.data.xAxis,
                  axisPointer: {
                    type: 'shadow'
                  }
                }
              ],
              yAxis: [
                {
                  type: 'value',//
                  name: yAxisName,//y
                  axisLabel: {
                    formatter: '{value}' // 
                  }
                }
              ],
              series: series//
            };
            statistic.setOption(option, true);
            window.onresize = function () {
              statistic.resize();
            };
          } else {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          }
        });
      });
      ////
      //_this.chartVisiable = true;
      // this.$nextTick(()=>{
      //     var statistic = this.$echarts.init(document.getElementById("statistic"),'macarons');
      //     let params = {
      //         tableName: "nongzi",
      //         groupColumn: "nongzi_types",
      //// riqi :getMonthFormat(_this.echartsDate),//
      //     }
      //     this.$http({
      //         url: "newSelectGroupCount",//pieSum pieCount
      //         method: "get",
      //         params: params
      //     }).then(({data}) => {
      //         if (data && data.code === 0) {
      //             let res = data.data;
      //             let xAxis = [];
      //             let yAxis = [];
      //             let pArray = []
      //             var option = {};
      //             for(let i=0;i<res.length;i++){
      //                 xAxis.push(res[i].name);
      //                 yAxis.push(res[i].value);
      //                 pArray.push({
      //                     value: res[i].value,
      //                     name: res[i].name
      //                 })
      //                 option = {
      //                     title: {
      //                         text: '',
      //                         left: 'center'
      //                     },
      //                     tooltip: {
      //                         trigger: 'item',
      //                         formatter: '{b} : {c} ({d}%)'
      //                     },
      //                     legend: {
      //                         orient: 'vertical',
      //                         left: 'left'
      //                     },
      //                     series: [
      //                         {
      //                             type: 'pie',
      //                             radius: '55%',
      //                             center: ['50%', '60%'],
      //                             data: pArray,
      //                             emphasis: {
      //                                 itemStyle: {
      //                                     shadowBlur: 10,
      //                                     shadowOffsetX: 0,
      //                                     shadowColor: 'rgba(0, 0, 0, 0.5)'
      //                                 }
      //                             }
      //                         }
      //                     ]
      //                 };
      //             }
      //             statistic.setOption(option,true);
      //             window.onresize = function() {
      //                 statistic.resize();
      //             };
      //         }
      //     });
      // })
    },
    contentStyleChange() {
      this.contentSearchStyleChange()
      this.contentBtnAdAllStyleChange()
      this.contentSearchBtnStyleChange()
      this.contentTableBtnStyleChange()
      this.contentPageStyleChange()
    },
    contentSearchStyleChange() {
      this.$nextTick(() => {
        document.querySelectorAll('.form-content .slt .el-input__inner').forEach(el => {
          let textAlign = 'left'
          if (this.contents.inputFontPosition == 2)
            textAlign = 'center'
          if (this.contents.inputFontPosition == 3) textAlign = 'right'
          el.style.textAlign = textAlign
          el.style.height = this.contents.inputHeight
          el.style.lineHeight = this.contents.inputHeight
          el.style.color = this.contents.inputFontColor
          el.style.fontSize = this.contents.inputFontSize
          el.style.borderWidth = this.contents.inputBorderWidth
          el.style.borderStyle = this.contents.inputBorderStyle
          el.style.borderColor = this.contents.inputBorderColor
          el.style.borderRadius = this.contents.inputBorderRadius
          el.style.backgroundColor = this.contents.inputBgColor
        })
        if (this.contents.inputTitle) {
          document.querySelectorAll('.form-content .slt .el-form-item__label').forEach(el => {
            el.style.color = this.contents.inputTitleColor
            el.style.fontSize = this.contents.inputTitleSize
            el.style.lineHeight = this.contents.inputHeight
          })
        }
        setTimeout(() => {
          document.querySelectorAll('.form-content .slt .el-input__prefix').forEach(el => {
            el.style.color = this.contents.inputIconColor
            el.style.lineHeight = this.contents.inputHeight
          })
          document.querySelectorAll('.form-content .slt .el-input__suffix').forEach(el => {
            el.style.color = this.contents.inputIconColor
            el.style.lineHeight = this.contents.inputHeight
          })
          document.querySelectorAll('.form-content .slt .el-input__icon').forEach(el => {
            el.style.lineHeight = this.contents.inputHeight
          })
        }, 10)
      })
    },
    // 
    contentSearchBtnStyleChange() {
      this.$nextTick(() => {
        document.querySelectorAll('.form-content .slt .el-button--success').forEach(el => {
          el.style.height = this.contents.searchBtnHeight
          el.style.color = this.contents.searchBtnFontColor
          el.style.fontSize = this.contents.searchBtnFontSize
          el.style.borderWidth = this.contents.searchBtnBorderWidth
          el.style.borderStyle = this.contents.searchBtnBorderStyle
          el.style.borderColor = this.contents.searchBtnBorderColor
          el.style.borderRadius = this.contents.searchBtnBorderRadius
          el.style.backgroundColor = this.contents.searchBtnBgColor
        })
      })
    },
    // 
    contentBtnAdAllStyleChange() {
      this.$nextTick(() => {
        document.querySelectorAll('.form-content .ad .el-button--success').forEach(el => {
          el.style.height = this.contents.btnAdAllHeight
          el.style.color = this.contents.btnAdAllAddFontColor
          el.style.fontSize = this.contents.btnAdAllFontSize
          el.style.borderWidth = this.contents.btnAdAllBorderWidth
          el.style.borderStyle = this.contents.btnAdAllBorderStyle
          el.style.borderColor = this.contents.btnAdAllBorderColor
          el.style.borderRadius = this.contents.btnAdAllBorderRadius
          el.style.backgroundColor = this.contents.btnAdAllAddBgColor
        })
        document.querySelectorAll('.form-content .ad .el-button--danger').forEach(el => {
          el.style.height = this.contents.btnAdAllHeight
          el.style.color = this.contents.btnAdAllDelFontColor
          el.style.fontSize = this.contents.btnAdAllFontSize
          el.style.borderWidth = this.contents.btnAdAllBorderWidth
          el.style.borderStyle = this.contents.btnAdAllBorderStyle
          el.style.borderColor = this.contents.btnAdAllBorderColor
          el.style.borderRadius = this.contents.btnAdAllBorderRadius
          el.style.backgroundColor = this.contents.btnAdAllDelBgColor
        })
        document.querySelectorAll('.form-content .ad .el-button--warning').forEach(el => {
          el.style.height = this.contents.btnAdAllHeight
          el.style.color = this.contents.btnAdAllWarnFontColor
          el.style.fontSize = this.contents.btnAdAllFontSize
          el.style.borderWidth = this.contents.btnAdAllBorderWidth
          el.style.borderStyle = this.contents.btnAdAllBorderStyle
          el.style.borderColor = this.contents.btnAdAllBorderColor
          el.style.borderRadius = this.contents.btnAdAllBorderRadius
          el.style.backgroundColor = this.contents.btnAdAllWarnBgColor
        })
      })
    },
    // 
    rowStyle({row, rowIndex}) {
      if (rowIndex % 2 == 1) {
        if (this.contents.tableStripe) {
          return {color: this.contents.tableStripeFontColor}
        }
      } else {
        return ''
      }
    },
    cellStyle({row, rowIndex}) {
      if (rowIndex % 2 == 1) {
        if (this.contents.tableStripe) {
          return {backgroundColor: this.contents.tableStripeBgColor}
        }
      } else {
        return ''
      }
    },
    headerRowStyle({row, rowIndex}) {
      return {color: this.contents.tableHeaderFontColor}
    },
    headerCellStyle({row, rowIndex}) {
      return {backgroundColor: this.contents.tableHeaderBgColor}
    },
    // 
    contentTableBtnStyleChange() {
      // this.$nextTick(()=>{
      //   setTimeout(()=>{
